import { Component, OnInit } from '@angular/core';
import { Employee } from 'src/app/model/employee.model';
import { EmployeeService } from '../../shared/employee.service';
import {ToastrService} from 'ngx-toastr';
@Component({
  selector: 'app-employee',
  templateUrl: './employee.component.html',
  styleUrls: ['./employee.component.scss']
})
export class EmployeeComponent implements OnInit {
  
  constructor(public empService:EmployeeService,private toastrService :ToastrService ) {
    
   }

  ngOnInit(): void {
   
  }
  createAndUpdate(currentEmployee:Employee){
  
    if(currentEmployee.id!=null)
    {
    console.table(currentEmployee);
    this.updateEmployee(currentEmployee);
     
    }else{
     
      this. createEmployee(currentEmployee);
    }

  }
  createEmployee(currentEmployee:Employee){
this.empService.createEmployee(currentEmployee).subscribe(
  (result:Employee)=>{
    this.clear();
    this.empService.getAllEmployee();
   
    this.toastrService.success("Employee Created Successfully","Employee Crud");
  }
)
;
  }

  updateEmployee(currentEmployee:Employee){
    
this.empService.updateEmployee(currentEmployee).subscribe(
  (result:Employee)=>{
    this.clear();
    this.empService.getAllEmployee();
    
    this.toastrService.info("Employee Updated Successfully","Employee Crud");
  } 
);
  }

  clear()
  {
    this.empService.currentEmployee={
      id:null,
      firstName:'',
      lastName:'',
    designation:'',
    contact:null,
    address:''
    }
  }

}
