import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { NgxSpinnerService } from 'ngx-spinner';
import { from, Observable } from 'rxjs';
import{Employee} from '../../model/employee.model';
const headerOption={
  headers:new HttpHeaders({'content-type':'application/json'})
};

@Injectable({
  providedIn: 'root'
})
export class EmployeeService {
mockUrl="http://localhost:3000/Employee";
allEmployee:Employee[];
currentEmployee:Employee={
  id:null,
  firstName:'',
  lastName:'',
designation:'',
contact:null,
address:''
};
  constructor(private http:HttpClient,private spinnerService:NgxSpinnerService) { }
  getAllEmployee(){
    this.spinnerService.show();
    return this.http.get<Employee[]>(this.mockUrl,headerOption).subscribe((data:Employee[])=>{
      this.allEmployee=data;
      setTimeout(()=>{
this.spinnerService.hide();
      },500);
        });;
  }
  deleteEmployee(id:number){
    return this.http.delete<Employee>(this.mockUrl+'/'+id,headerOption).subscribe((data:Employee)=>
    {
     this. getAllEmployee();
      
    });;
  }
  createEmployee(emp:Employee):Observable<Employee>{
    return this.http.post<Employee>(this.mockUrl,emp,headerOption);
  }
  updateEmployee(emp:Employee):Observable<Employee>{
   
    return this.http.put<Employee>(this.mockUrl+'/'+emp.id,emp);
  }
}
